<template lang="pug">
.page
    my-nav-bar(title="加入爱品车")
    van-cell-group
        van-field(label="电话" placeholder="请输入电话" )
        van-field(label="验证码" placeholder="请输入验证码" )
            van-button(slot="button" size="small" type="primary") 发送验证码
        my-form-select(label="类型" placeholder="请选择" :actions="[{name:'车商'},{name:'销售'}]")
        van-field(label="推荐人" disabled v-model="tuijian")
        van-field(label="真实姓名" placeholder="请输入真实姓名")
        my-form-select(label="性别" placeholder="请选择" :actions="[{name:'男'},{name:'女'}]")
        van-field(label="所在城市" disabled v-model="city")
        van-field(label="身份证号" placeholder="请输入身份证号")
    my-bottom-bar.bottom(:click="save")
        .g-bottom-btn 提交
</template>

<script>
export default {
    data () {
        return {
            tuijian: '刘剑锋',
            city: '成都'
        };
    },
    methods: {
        save () {}
    },
    async created () {
        try {
            let {data} = await this.$http.post('/mobile/business/info', {p: 1});
            console.log(data);
        } catch (error) {
            console.log(error);
        }
    }
};
</script>



